import React, { useState, useEffect } from "react";
import Formulario from "./components/Formulario";
import Cancion from "./components/Cancion";
import Info from "./components/Info";

import axios from "axios";

function App() {

  //definir el state
  const [ busquedaletra, guardarBusquedaLetra ] = useState({});
  const [ letra, guardarLetra ] = useState("");
  const [ info, guardarInfo ] = useState({});
  const [ error, guardarError ] = useState(false);

  useEffect(() => {
    
    if(Object.keys(busquedaletra).length === 0) return;

    const consultarApiLetra = async () => {

        const { artista, cancion } = busquedaletra;
        const url = `https://api.lyrics.ovh/v1/${artista}/${cancion}`;
        const url2 = `https://www.theaudiodb.com/api/v1/json/1/search.php?s=${artista}`;

        axios.all([
          axios.get(url),
          axios.get(url2)
          ]).then(axios.spread((letra, informacion) => {
          guardarLetra(letra.data.lyrics);
          guardarInfo(informacion.data.artists[0]);
          })).catch(error => {
          guardarError(true);
          });
          guardarError(false);
        
    }
   consultarApiLetra();
  }, [busquedaletra])
  return (
    <>
      <Formulario 
        guardarBusquedaLetra={guardarBusquedaLetra}
      />
      <div className="container">
          {error ? <p className="alert alert-primary my-3 p-5 text-center">Artista o canción no encontrada</p> : 

            ( <div className="row">
                  <div className="col-md-6">
                    <Info 
                      info={info}
                    />
                  </div>
                  <div className="col-md-6">
                    <Cancion 
                      letra={letra}  
                    />
                     {letra.length === 0 && Object.keys(info).length !== 0 ? (
                      <p className="alert alert-primary text-center mt-4">
                       No se encontró la letra de la canción
                       </p>
                       ) : null}
                  </div>
            </div>
            )}
        </div>
    </>
  );
}

export default App;
